import {get, post, getImage, postImg } from '@/utils/request';

if (process.env.NODE_ENV === "development") {
    var baseURL = 'http://app.ipv6gz.com:8088';
} else {
    // var baseURL = 'http://10.0.0.10:9000';
    var baseURL = 'http://app.ipv6gz.com:8088';
    // app.mogume.com:9000
    // app.lovemoguyun.com:9000
    // app.aimoguyun.com:9000
}

//云集列表
export const padList = params => {
    return get(`${baseURL}/pad/search`, params).then(
        res => res.data
    );
};

//修改云机名
export const updatePadName = params => {
    return get(`${baseURL}/pad/updateUserPadName`, params).then(
        res => res.data
    );
};

//云机套餐
export const padGoodsList = params => {
    return get(`${baseURL}/goods/list`, params).then(
        res => res.data
    );
};

//绑定云机
export const bindPad = params => {
    return get(`${baseURL}/pad/bind`, params).then(
        res => res.data
    );
};

//解绑定云机
export const relievePad = params => {
    return get(`${baseURL}/pad/relieve`, params).then(
        res => res.data
    );
};

//延长到期时间
export const addExpireTime = params => {
    return get(`${baseURL}/pad/addExpireTime`, params).then(
        res => res.data
    );
};

//重启云机
export const rebootPads = params => {
    return get(`${baseURL}/pad/rebootPads`, params).then(
        res => res.data
    );
};

//重置云机
export const resetPads = params => {
    return get(`${baseURL}/pad/resetPads`, params).then(
        res => res.data
    );
};


export const baseInfo = params => {
    return get(`${baseURL}/deviceDetail/baseInfo`, params).then(
        res => res.data
    );
};

export const detailInfo = params => {
    return get(`${baseURL}/deviceDetail/detailInfo`, params).then(
        res => res.data
    );
};




